.goods {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	.product-slide__wrap {
		min-height: 394px;
		margin-bottom: 28px;
		margin-right: 0;
	}
	.product-slide__wrap:nth-child(4n+4) {
		margin-right: 0;
	}
	&-box {
		margin-top: 60px;
	}
	&__pages {
		display: flex;
		width: 100%;
		justify-content: center;
		margin-bottom: 47px;
		margin-top: 13px;
		font-size: 14px;
		position: relative;
		z-index: 0;
		span {
			line-height: 1.43;
			color: rgba(#292930, 0.8);
			background-color: $color-bg;
			padding-left: 16px;
			padding-right: 16px;
		}
		&::before {
			content: '';
			position: absolute;
			width: 100%;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			height: 1px;
			background-color: #c0bcab;
			z-index: -1;
		}
	}
}