.top-menu {
	display: flex;
	&__box {
		display: flex;
		height: 56px;
		align-items: center;
		position: relative;
	}
	&__box:first-child {
		width: 25%;
	}
	&__box:nth-child(2) {
		width: 60%;
		padding-left: 27px;
	}
	&__box:last-child {
		width: 15%;
		padding-left: 15px;
	}
	&__list {
		display: flex;
		height: 100%;
		li {
			height: 100%;
		}
	}
	&__tel {
		font-family: $font-firaM;
		font-weight: 500;
		line-height: 56px;
		letter-spacing: 0.1px;
		margin-right: 17px;
		height: 100%;
	}
	&__link {
		display: block;
		height: 100%;
		padding-right: 9px;
		padding-left: 9px;
		line-height: 56px;
		span {
			border-bottom: 1px solid rgba($color-main2, 0.3);
			transition: border-bottom-color .3s;
		}
		&:hover {
			span {
				border-bottom-color: rgba($color-main2, 0.6);
			}
		}
	}
	&__user {
		height: 100%;
		line-height: 56px;
		span {
			margin-left: 10px;
			border-bottom: 1px dashed rgba($color-main2, 0.3);
			transition: border-bottom-color .3s;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			max-width: 126px;
			line-height: normal;
		}
		&--active span{
			color: $color-blue5;
			border-bottom: 1px dashed rgba($color-blue5, 0.3);
		}
		&--active svg use {
			fill: $color-blue5;
		}
		&:hover {
			span {
				border-bottom-color: rgba($color-main2, 0.6);
			}
		}
		&--none {
			display: none;
		}
		&--block {
			display: flex;
			align-items: center;
			justify-content: center;
			cursor: pointer;
			span {
				max-width: 125px;
			}
			.svg-arrow {
				margin-left: 6px;
				use {
					fill: $color-main2;
					stroke: $color-main2;
				}
			}
		}
	}
	&__modal {
		overflow: hidden;
		max-height: 0;
		position: absolute;
		top: 45px;
		right: 0;
		z-index: 300;
		transition: max-height .3s;
		&--active {
			max-height: 500px;
		}
		&-wrap {
			border-radius: 3px;
			border: solid 1px #c5c5ca;
			background-color: $color-white;
			box-shadow: 0 2px 6px 0 rgba(35, 35, 42, 0.26);
		}
		a {
			padding: 9px 24px 12px 20px;
			display: block;
			white-space: nowrap;
			&:hover {
				background-image: linear-gradient(to right, rgba(94, 77, 249, 0), rgba(57, 49, 133, 0.13));
			}
			&:last-child {
				border-top: 1px solid #c5c5ca;
			}
		}
	}
}