.account .title-left {
	display: none;
}
.account-wrap {
	flex-direction: column-reverse;
	.advantage__right {
		display: block;
		background-color: transparent;
		border: none;
		width: 100%;
		padding: 0;
	}
	.advantage__item:nth-child(3) {
		display: none;
	}
	.advantage__item:nth-child(4) {
		display: none;
	}
	.advantage__list {
		display: flex;
		margin-top: -15px;
		margin-bottom: 20px;
	}
	.advantage__item {
		margin-right: 19px;
	}
	.advantage__item a span {
		padding-bottom: 7px;
		border-bottom: none;
		font-size: 18px;
	}
	.advantage__item--active a span {
		border-bottom: 3px solid $color-blue5;
	}
}
.account-left {
	width: 100%;
	background-color: transparent;
	padding: 0;
	border: none;
}
.account__wrap {
	background-color: #fff;
	padding: 37px 15px 0;
}
.account {
	.account__label {
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 21px;
		br {
			display: none;
		}
	}
	.account__label input {
		width: 100%;
		max-width: 100%;
		margin-top: 9px;
	}
}
.account .modal__pas {
	left: auto;
	right: 12px;
	top: auto;
	bottom: 14px;
}
.account__item {
	position: relative;
	padding: 22px 0 28px;
}
.link-solid-blue-s > span {
	font-size: 18px;
}
.account__prices {
	position: absolute;
	right: 0;
	top: 25px;
}
.account__title {
	font-size: 18px;
	margin-bottom: 22px;
}
.account__data {
	font-size: 14px;
	margin-top: 10px;
}
.history-order {
	background-color: #fff;
	border: 1px solid #e1e1e2;
	border-radius: 3px;
	padding: 15px 14px 41px;
}
.account__top {
	flex-direction: column-reverse;
	align-items: flex-start;
	position: relative;
	.link-dashed-s {
		position: absolute;
		bottom: -54px;
		left: 0;
		margin-left: 0;
		font-size: 14px;
	}
}
.account__prev {
	margin-bottom: 23px;
}
.history-order .account__check {
	margin-top: 6px;
	padding-bottom: 50px;
	span {
		font-size: 14px;
	}
}
.history-order .account__no-price {
	display: none;
}
.history-order .account__item {
	display: inline-block;
	width: 100%;
	padding: 22px 0 23px;
}
.history-order .basket__box {
	width: 100%;
	padding-right: 0;
}
.history-order .basket__img {
	display: inline-block;
	float: left;
}
.history-order .account__prices {
	position: relative;
	top: auto;
	right: auto;
	display: inline-block;
	float: left;
	margin-top: 3px;
}
.history-order .basket__img {
	width: 70px;
	height: 70px;
	border: 1px solid #e1e1e2;
	margin-right: 15px;
}
.history-order .account__price {
	font-size: 16px;
}
.history-order__summ {
	text-align: left;
	border-top: 1px solid #e1e1e2;
	font-size: 16px;
	padding-top: 20px;
	padding-bottom: 2px;
}
.history-order .account__item::after {
	content: '';
	display: table;
	clear: both;
}
.history-order__summ::before {
	border: none
}
.account__bottom {
	margin-top: 43px;
}
.account__password {
	margin-top: 14px;
	padding: 23px 15px 28px;
}
.account .btn-green {
	margin-right: 12px;
}
.account .btn-grad {
	padding: 3px 19px 5px;
}
.account__wrap:last-child {
	margin-top: 14px;
}
.history-order .basket__name {
	margin-bottom: 6px;
}
