.offers {
  margin-top: 0;
  padding-top: 20px;
  padding-bottom: 8px;
}
.offers__list {
  flex-flow: nowrap;
  flex-shrink: 0;
  width: auto;
}
.offers__list li{
  display: inline-block !important;
  flex-shrink: 0;
}
.offers__link {
  display: block;
  margin-bottom: 0;
}
.offers__toggle {
  display: none !important;
}
.nav-menu .offers__link {
  margin-bottom: 8px;
}