.header-checkout {
	flex-direction: column;
	align-items: flex-start;
	padding-top: 9px;
	padding-bottom: 17px;
}
.svg-logo-checkout {
	height: 50px;
}
.header-checkout__service {
	margin-left: 0;
	margin-right: 0;
	font-weight: normal;
	font-size: 14px;
}
.header-checkout__tel {
	display: inline-block;
	margin-left: 7px;
}
.header-checkout__time {
	font-size: 14px;
	font-weight: normal;
	margin-top: 5px;
}
.header-checkout__txt {
	display: inline-block;
	margin-top: -2px;
	font-size: 14px;
}
.checkout {
	flex-direction: column-reverse;
}
.checkout__right {
	width: 100%;
}
.checkout__left {
	width: 100%;
}
.checkout__main {
}
.checkout__bottom {
}
.checkout__tab {
	padding: 13px 7px 0;
}
.checkout__tab:first-child {
	padding-left: 16px;
}
.checkout__desc-bottom {
	padding: 27px 16px 33px;
}
.checkout__label {
	max-width: 100%;
	margin-bottom: 23px;
}
.checkout__label input {
	max-width: 100%;
}
.checkout__box {
	flex-direction: column;
	align-items: flex-start;
	padding-top: 11px;
	margin-bottom: 16px;
}
.checkout__error {
	margin-left: 0;
	margin-top: 9px;
}
.checkout__top a.checkout__link {
	display: none;
}
.checkout .checkout__link.desc-hidden {
	margin-top: 10px;
	margin-bottom: 23px;
	margin-left: 0;
}
.checkout__top-prices {
	display: flex;
	margin-top: 1px;
	.checkout__no-price {
		color: $color-red;
		margin-left: 15px;
	}
}
.checkout__top {
	position: relative;
	&::after {
		content: '';
		position: absolute;
		top: 21px;
		right: 18px;
		width: 10px;
		height: 5.6px;
		background: url("../images/sprites/svg/arrow.svg") center no-repeat;
		-webkit-background-size: cover;
		background-size: cover;
	}
}
.checkout__top {
	border-bottom: none;
	&--active {
		border-bottom: 1px solid #e9e4d0;
	}
}
.checkout__right-wrap {
	overflow: hidden;
	max-height: 0;
	transition: max-height .3s;
	&--active {
		max-height: 3000px;
	}
}
.checkout__sale {
	display: none;
}
.modal__address-link {
	min-width: 100%;
	width: 100%;
	span {
		display: inline-block;
		flex-shrink: 0;
		max-width: 248px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
}
.checkout__city-link {
	width: 100%;
	margin-bottom: 23px;
}
.checkout__tabs-delivery {
	margin-bottom: 23px;
}
.checkout .about__tab {
	width: 50%;
}
.checkout__textarea {
	margin-top: 21px;
}
.checkout__textarea textarea {
	max-width: 100%;
	width: 100%;
}
.checkout .modal__min {
	margin-top: 10px;
	margin-bottom: 13px;
}
.checkout .modal__min--mb {
	margin-bottom: 17px;
	br {
		display: none;
	}
}
.checkout__date {
	width: 100%;
}
.checkout .checkout__tabs-pay .about__tab {
	padding-bottom: 15px;
	height: max-content;
}
.checkout .checkout__tabs-pay .about__tab:last-child {
	padding-right: 10px;
}
.checkout__final span br{
	display: none;
}
.checkout-ok {
	padding: 46px 16px 47px;
	margin-top: 66px;
}
.checkout-ok__title {
	font-size: 20px;
}
.checkout-ok__title span {
	display: block;
}
.checkout-ok__address span {
	margin-top: 8px;
}
.checkout-ok__detail .link-solid-blue-s {
	display: block;
}
.checkout-ok__tel {
	display: block;
}
.checkout-ok__subtitle {
	max-width: 280px;
	margin-left: auto;
	margin-right: auto;
}
.checkout__desc-wrap:first-child {
	.checkout__label:nth-child(2) {
		margin-bottom: 14px;
	}
}