.registration-modal {
	min-width: 100vw;
	width: 100vw;
}
.registration-modal .modal__label {
	flex-direction: column;
	br {
		display: none;
	}
}
.registration-modal .modal__label span {
	width: auto;
	margin-right: 0;
	margin-bottom: 9px;
}
.modal__form {
	width: 100%;
	padding: 0 53px;
}
.registration-modal .modal__form {
	padding: 0;
}
.registration-modal .btn {
	margin-left: auto;
	margin-right: auto;
	width: 100%;
}
.registration-modal .modal__wrap {
	padding-left: 82px;
	padding-right: 82px;
}
.registration-modal .modal__check {
	margin-left: 0;
	a {
		margin-left: 29px;
	}
}
.modal__pas--reg {
	left: auto;
	right: 15px;
	top: 46px;
}