.product-slide {
  position: relative;
  height: auto;
  display: block;
  width: 100vw;
}
.product-slide__wrapper {
  min-height: 241px;
}
.product-slide__bottom {
  display: none;
}
.product-slide__desc {
  display: none;
}
.product-slide__box {
  display: none;
}
.product-slide__price .product-slide__no-price {
  display: block;
}
.product-slide__wrap {
  min-height: 241px;
  width: 44.2vw;
  margin-right: 8px;
}
.product-slide__title {
  font-size: 0.875rem;
  height: 17px;
  overflow: hidden;
  margin-bottom: 3px;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 34vw;
}
.product-slide__price .product-slide__no-price {
  font-size: 0.875rem;
}
.product-slide__price span {
  font-size: 0.875rem;
}
.product__arrow {
  display: none !important;
}
.product-slide__top {
  width: 100%;
}
.product-slide__item {
  padding: 8px 11px 17px;
}
.product-slide__top-item img {
  max-width: 100%;
  max-height: 100%;
}
.product-slide__top-item {
  height: auto;
  display: none !important;
  min-height: 104px;
}
.product-slide__top-item:first-child {
  display: flex !important;
}
.product-slide__top {
  height: auto;
  min-height: 104px;
}
.product-slide--right {
  top: 8px;
}
.product-slide__like {
  top: 0;
  left: 0;
  width: 22px;
  height: 20px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.product-slide__like svg {
  width: 14px;
  height: 12px;
}
.product-slide__price {
  margin-top: 8px;
}
