.doggle-menu {
	position: relative;
	top: 0;
	width: 100%;
	max-height: 0;
	opacity: 0;
	visibility: hidden;
	&--active {
		max-height: 3000px;
		opacity: 1;
		visibility: visible;
	}
}
.doggle-menu__wrap {
	padding: 0;
}
.doggle-menu__offers {
	.offers__list {
		flex-wrap: wrap;
		max-height: max-content;
	}
	.offers__toggle {
		display: none;
	}
	.offers {
		padding: 17px 16px 12px;
		margin-top: 0;
	}
}
.doggle-menu__brend {
	display: none;
}
.doggle-menu__title {
	display: none;
}
.doggle-menu__categories {
	margin-right: 0;
	width: 100%;
}
.doggle-menu__cattalog {
	padding: 0;
}
.doggle-menu__list {
	max-height: none;
}
.doggle-menu__list li {
	width: 100%;
	margin-right: 0;
	&:last-child .doggle-menu__item{
		border-bottom: none;
	}
}
.doggle-menu__item {
	font-size: 14px;
	line-height: 1.43;
	color: #292930;
	background-color: $color-white;
	width: 100%;
	padding: 13px 16px 14px;
	font-family: $font-fira;
	font-weight: normal;
	border-bottom: 1px solid #e1e1e2;

	span {
		border-bottom: none;
	}
}