.address {
	display: flex;
	flex-direction: column-reverse;
	margin-bottom: -29px;
	.map {
		margin-top: -6px;
		min-height: 283px;
	}
	.card-product__arrow {
		display: none !important;
	}
}
.address__list {
	flex-direction: column;
}
#map {
	min-height: 283px;
	height: 100% !important;
}
.address__item--active::before {
	top: 0;
	left: 0;
	display: none;
}
.address__item {
	width: 100%;
	list-style: none;
	margin-bottom: 0;
}
.address__wrap {
	position: relative;
	z-index: 1;
	padding-bottom: 6px;
	margin-bottom: 25px;
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: calc(50% - 50vw);
		width: 110vw;
		height: 100%;
		background-color: #f1edde;
		border-top: 1px solid #e4e1d3;;
		border-bottom: 1px solid #e4e1d3;;
		z-index: -1;
	}
}
.title-bread {
	font-size: 20px;
	margin-top: 57px;
	margin-bottom: 22px;
	letter-spacing: normal;
}
.address__time {
	margin-bottom: 9px;
}
.address__subtitle {
	font-size: 16px;
	margin-top: 20px;
}
.address__slider-pop {
	width: 100%;
}
.address__slide-pop-img {
	width: 100%;
	height: max-content;
}
.mfp-wrap .address__slider-pop {
	opacity: 1;
	visibility: visible;
	height: max-content;
}

