.main-slider {
	min-height: 148px;
	height: 39vw;
	margin-top: 21px;
	width: 100vw;
	background-color: transparent;
	box-shadow: none;
	display: flex;
}
.main-slider__item {
	width: 88.5vw;
	margin-right: 12px;
}
.internet-price__title--mobile {
	display: block;
	margin: 0 auto 28px;
	line-height: 1.43;
}
.internet-price__title--desc {
	display: none;
}
.internet-price {
	flex-direction: column;
	margin-top: 45px;
}
.internet-price__img {
	margin: 0 auto 24px;
	width: 64%;
	height: auto;
}
.internet-price__txt {
	padding-right: 0;
}
.internet-price__title {
	font-size: 1.25rem;
	text-align: center;
}
.internet-price__item {
	font-size: 1rem;
	br {
		display: none;
	}
}
.prime {
	display: none;
}
#our-shops {
	display: none;
}
.main-slider {
	overflow: visible;
}