.toolbar {
	margin-top: -1px;
	margin-bottom: 13px;
	flex-direction: column-reverse;
	align-items: flex-start;
	.modal__check {
		display: none;
	}
	&__box:nth-child(3) {
		display: flex;
		font-style: italic;
		margin-bottom: 8px;
	}
	&__box:nth-child(4) {
		display: none;
	}
	.link-modal {
		left: -18px;
	}
}