		letter-spacing: normal;
	}
}
.advantage__left {
	width: 100%;
	padding: 33px 19px 61px;
}
.advantage__discount-img {
	width: 100%;
	max-height: 200px;
	margin-top: 25px;
	margin-left: 0;
	margin-bottom: 19px;
}
.advantage .par {
