.card-product__slider-bottom {
	display: none;
}
.card-product__slider {
	height: 68.27vw;
	margin-bottom: 12px;
}
.card-product {
	flex-direction: column;
	margin-top: 0;
}
.card-product__filter-wrap {
	position: relative;
	top: 0;
	left: 0;
	transform: translate(0);
	width: 100%;
	height: auto;
	margin-bottom: 12px;
}
.card-product__filter {
	position: relative;
	width: 100%;
	padding: 33px 16px 37px;
	box-shadow: none;
	border: none;
}
.card-product__box--left {
	width: 100%;
}
.card-product__box--right {
	width: 100%;
}
.card-product__slider-top {
	margin-bottom: 0 !important;
}
.card-product__item-top {
	height: 100%;
}
.card-product__item-top img {
	max-width: 100%;
	max-height: 100%;
}
.card-product__art {
	font-size: 14px;
	margin-bottom: 20px;
}
.card-product__filter {
	.card-product__title {
		display: none;
	}
	.card-product__art {
		display: none;
	}
	.product-slide__box {
		display: flex;
		flex-direction: row;
		margin-bottom: 9px;
		.btn-grad {
			margin-right: 11px;
			padding: 11px 16px 13px;
			font-size: 15px;
			line-height: 1.6;
		}
		.btn-reds {
			padding: 11px 26px 13px;
			font-family: $font-firaM;
			font-size: 17px;
			line-height: 1.41;
		}
		a {
			text-transform: capitalize;
			margin-bottom: 0;
		}
		span {
			display: none;
		}
	}
	.product-slide__price {
		display: flex;
		align-items: flex-end;
		margin-top: 27px;
		span {
			font-size: 23px;
		}
		.product-slide__no-price {
			font-size: 17px;
		}
	}
}
.card-product__list {
	margin-bottom: 10px;
}
.card-product__tab {
	padding: 17px 0 0 15px;
	text-transform: capitalize;
	font-weight: normal;
	letter-spacing: normal;
}
.card-product__desc-bottom {
	padding: 26px 14px 22px;
}
.card-product__tr td {
	font-size: 14px;
	padding-right: 0;
	padding-bottom: 14px;
	br {
		display: none;
	}
	&:first-child {
		min-width: 182px;
	}
	&:last-child {
		display: flex;
		flex-wrap: wrap;
	}
}
.card-product__slider-top .main-slider__arrow--next {
	right: -41.5px;
}
.card-product__slider-top .main-slider__arrow--prev {
	left: -41.5px;
}
.card-product__slider-top .slick-dots {
	bottom: -27px;
}
.card-product__guarantee {
	margin-bottom: 8px;
}
.new-product .subtitle {
	text-align: center;
	margin-top: 48px;
	font-size: 20px;
	margin-bottom: 31px;
}