.nav-menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 56px;
	border-radius: 0 0 4px 4px;
	&__list-wrap {
		position: fixed;
		top: 0;
		left: calc(-100vw + 56px);
		display: flex;
		flex-direction: column;
		width: calc(100vw - 56px);
		z-index: 3000;
		max-height: 100vh;
		overflow: hidden;
		transition: left .3s;
		height: auto;
		&--active {
			left: 0;
		}
	}
	&__burger {
		overflow-y: scroll;
		padding-bottom: 70px;
		background-color: #ffffff;
		height: auto;
	}
	&__list {
		flex-direction: column;
		li {
			height: auto;
		}
	}
	&__link {
		justify-content: space-between;
		background-color: #292930;
		width: 100%;
		color: $color-white;
		padding: 13px 16px 14px;
		font-size: 14px;
		font-family: $font-fira;
		font-weight: normal;
		line-height: 1.43;
		border-top: 1px solid #18181d;
		text-transform: capitalize;
		&--active {
			svg {
				transform: rotate(180deg);
			}
		}
	}
	&__search {
		padding: 0 24px 0 0;
	}
	&__shop {
		margin-left: 14px;
	}
	&__box {
		margin-right: 16px;
		span {
			display: none;
		}
		a {
			padding: 0;
			&::before {
				background-color: transparent;
			}
		}
	}
	&__logo {
		padding-left: 0;
		margin-left: 76px;
		svg {
			width: 127px;
			height: 28px;
		}
	}
}
.top-menu {
	flex-direction: column;
	&__list {
		width: 100%;
	}
	&__link {
		font-size: 14px;
		line-height: 1.43;
		color: #292930;
		background-color: $color-white;
		width: 100%;
		padding: 13px 16px 14px;
		font-family: $font-fira;
		font-weight: normal;
		border-bottom: 1px solid #e1e1e2;
		span {
			border-bottom: none;
		}
	}
	&__box {
		background-color: $color-white;
	}
	&__user {
		line-height: 1.43;
		font-size: 14px;
		padding-top: 12px;
		padding-bottom: 15px;
		width: 100%;
		justify-content: flex-start;
		span {
			border-bottom: none;
		}
	}
	&__tel {
		font-size: 14px;
		font-weight: 500;
		line-height: 1.43;
		padding-top: 16px;
		padding-bottom: 27px;
	}
	&__box {
		width: 100%;
		height: auto;
		&:first-child {
			width: 100%;
		}
		&:last-child {
			width: 100%;
			padding-left: 16px;
		}
		&:nth-child(2) {
			width: 100%;
			border-bottom: 1px solid #e1e1e2;
			flex-direction: column;
			padding-left: 0;
		}
	}
}
.top-menu__list {
	flex-direction: column;
}
.burger-menu__title {
	background-color: #292930;
	width: 100%;
	color: $color-white;
	padding: 17px 16px 19px;
	font-family: $font-firaM;
	font-size: 16px;
	line-height: 1.25;
}
.nav-menu__search--active {
	margin-right: 21px;
}
.nav-menu__search-wrap {
	right: -15px;
}
.nav-menu__link--active svg use {
	fill: $color-white;
	stroke: $color-white;
}
.nav-menu__box a.nav-menu__link-like {
	position: fixed;
	top: 0;
	right: 60px;
	width: 42px;
	height: 56px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.nav-menu__box .svg-like {
	margin-right: 0;
}
.nav-menu__box a.nav-menu__link-basket {
	position: fixed;
	top: 0;
	right: 0;
	width: 60px;
	height: 56px;
}
.nav-menu__search {
	position: fixed;
	top: 8px;
	right: 102px;
	height: 40px;
	width: 40px;
	z-index: 251;
	margin: 0;
	padding: 0;
}
.nav-menu__search-wrap {
	top: 50%;
	transform: translateY(-50%);
	right: 0;
}
.nav-menu__search-block {
	left: 233px;
}
.top-menu__user--block span {
	max-width: max-content;
}
.top-menu__user--active .svg-arrow {
	transform: rotate(180deg);
}
.top-menu__user--block .svg-arrow {
	position: absolute;
	top: 16px;
	right: 16px;
}
.top-menu__user {
	padding: 13px 16px 16px;
}
.top-menu__modal {
	display: block;
	position: relative;
	right: auto;
	top: auto;
	width: 100%;
	transition: none;
}
.top-menu__modal-wrap {
	border-bottom: none;
	border-left: none;
	border-right: none;
	border-radius: 0;
	border-color: #e1e1e2;
	a {
		padding: 17px 16px 19px;
	}
	a:last-child {
		border-color: #e1e1e2;
	}
}
.top-menu__modal-item {
	font-size: 14px;
	padding: 13px 16px 16px;
}
.top-menu__modal-wrap a {
	padding: 13px 16px 16px;
}

