.no-result {
	.title {
		text-align: left;
		font-size: 20px;
		margin: 0 0 18px;
	}
	.desc {
		text-align: left;
		font-size: 18px;
	}
}
.fast-catalog__box:first-child {
	width: auto;
	display: flex;
}
.fast-catalog__box:last-child {
	width: auto;
	display: flex;
}
.result {
	padding-top: 0;
	margin-top: -6px;
	.modal__check {
		display: none;
	}
}
.result__link {
	font-size: 16px;
	margin-bottom: 10px;
}
.result__list {
	justify-content: space-between;
	margin-top: 19px;
}
.result__list .product-slide__wrap {
	min-height: 236px;
	margin-right: 0;
	margin-bottom: 12px;
}
.no-result-404 {
	margin-top: 57px;
}
.no-result-404 .desc {
	margin-bottom: 39px;
}