.fast-catalog {
  flex-shrink: 0;
  margin-top: 32px;
  height: max-content;
  width: 100vw;
}
.fast-catalog__wrapper {
  flex-shrink: 0;
  width: 88.5vw;
  margin-right: 12px;
  min-height: 369px;
  padding: 34px 24px 44px;
}
.fast-catalog__list {
  max-height: 270px;
}
.fast-catalog a.fast-catalog__title span {
  font-size: 1.438rem;
}
.fast-catalog__wrapper a span {
  font-weight: normal;
  font-size: 1.063rem;
}