.title {
  font-size:  1.25rem;
  margin: 45px 0 31px;
}
.title--desc {
  margin-bottom: 11px;
}
.title-left {
  font-size: 20px;
  margin: 57px 0 30px;
}
.title-left.title--desc {
  margin-bottom: 6px;
}
.desc {
  font-size: 1rem;
}
.link-catalog {
  .desc {
    width: 223px;
    margin: 0 auto;
  }
}
.burger-menu {
  display: block;
  position: fixed;
  z-index: 250;
  top: 0;
  left: 0;
  height: 56px;
  width: 56px;
  transition: left .3s;
  background-color: $color-red;
  pointer-events: auto;
  border-radius: 0 0 0 3px;
  &--active {
    left: calc(100vw - 56px);
    span {
      background-color: transparent !important;
      &::before {
       top: 0 !important;
        transform: rotate(45deg);
      }
      &::after {
        top: 0 !important;
        transform: rotate(-45deg);
      }
    }
  }
  &__title {
    display: block;
  }
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    width: 1px;
    height: 40px;
    background-color: #b91b12;
  }
  span {
    position: absolute;
    width: 18px;
    height: 2px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: $color-white;
    transition: background-color .3s;
    &::before {
      content: '';
      position: absolute;
      width: 18px;
      height: 2px;
      left: 0;
      top: 6px;
      background-color: $color-white;
    }
    &::after {
      content: '';
      position: absolute;
      width: 18px;
      height: 2px;
      left: 0;
      top: -6px;
      background-color: $color-white;
    }
  }
}
.body-fix {
  position: relative;
  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba($color-main2, 0.3);
    z-index: 100;
  }
}
.pagination__btn {
  width: 100%;
}
.fixed-btn-up {
  display: none;
}