html,
body {
	font-size: 16px;
	min-width: auto;
}
.wrapper {
	width: 100vw;
	overflow: hidden;
	height: max-content;
	min-height: 100%;
}
body {
	padding-top: 56px;
}
.container {
	margin-right: auto;
	margin-left: auto;
	width: 91.5vw;
	padding: 0;
}
.top-menu {
	display: none;
}
.top-menu.top-menu--mobile {
	display: flex !important;
}
.desc-hidden {
	display: block !important;
}
.mob-hidden {
	display: none;
}