.bx-filter {
	background-color: transparent;
	border: none;
	padding: 0;
	.svg-filter {
		display: none;
	}
}
.smartfilter {
	display: flex;
	flex-direction: column-reverse;
}
.bx-filter-button-box {
	border-radius: 4px;
	background-color: #292930;
	padding: 13px 16px 16px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	margin-bottom: 8px;
	max-height: 48px;
	&::before {
		content: 'Фильтр';
		color: $color-white;
		font-family: $font-firaM;
		font-size: 16px;
	}
}
.bx-filter-button-box .btn-reds {
	display: none;
}
.bx-filter-dump {
	display: none;
	background-color: #d0271e;
	padding: 3px 12px 5px;
	font-size: 13px;
	border: 1px solid #d0271e;
	cursor: pointer;
	color: $color-white;
	border-radius: 2px;
	transition: background-color .3s, color .3s, box-shadow .3s, border-color .3s, -webkit-box-shadow .3s, -moz-box-shadow .3s;
	text-decoration: none;
	margin-left: 0;
	&--active {
		display: inline-block;
	}
	span {
		border: none;
		font-weight: normal;
	}
}
.smartfilter__box {
	background-color: $color-white;
	border-bottom: none;
	border-radius: 2px;
	padding: 0;
	overflow: hidden;
	max-height: 0;
	transition: max-height .3s;
	&--active {
		max-height: 3000px;
	}
}
.bx-filter-parameters-box {
	width: 100%;
	border-bottom: 1px solid #e4e5e8;
	padding: 0;
	margin-bottom: 0;
}
.bx-filter-parameters-box-title {
	padding: 13px 16px 15px;
	flex-wrap: wrap;
}
.bx-filter-mobile-wrap {
	width: 100%;
}
.bx-filter-desc {
	position: relative;
	left: 0;
	top: 0;
	margin-top: 1px;
	margin-left: -3px;
	width: 100%;
}
.bx-filter-block {
	position: relative;
	top: 0;
	box-shadow: none;
	min-width: auto;
	border-radius: 0;
	border-top: 1px solid #e4e5e8;
	padding-top: 27px;
}
.bx-filter-desc-box {
	width: max-content;
}
.bx-filter-block::before {
	background-color: #e4e5e8;
}
.bx-filter-parameters-box--active {
	.bx-filter-parameters-box-title {
		padding-top: 6px;
		padding-bottom: 3px;
	}
}
.bx-filter-parameters-box-title span {
	border-bottom: none;
	margin-right: 2px;
}
.bx-filter-box {
	padding-bottom: 13px;
}