.breadcrumbs {
	display: flex;
	margin-top: 18px;
	&__link {
		display: flex;
		align-items: center;
		font-size: 0.9375rem;
		color: rgba(45, 43, 35, 0.77);
		span {
			border-bottom: 1px solid rgba(45, 43, 35, 0.3);
			transition: border-bottom-color .3s;
		}
		&:hover span{
			border-bottom: 1px solid rgba(45, 43, 35, 0.6);
		}
		&:last-child {
			cursor: default;
			opacity: 0.8;
			span {
				border-color: transparent;
			}
			&:hover span{
				border-color: transparent;
			}
		}
	}
}