.basket {
	background-color: transparent;
	border: none;
}
.basket__form {
	margin-top: 25px;
}
.basket__list {
	padding: 0;
}
.basket__item {
	position: relative;
	border: 1px solid #e4e1d3;
	margin-bottom: 12px;
	background-color: $color-white;
	border-radius: 4px;
	display: inline-block;
	width: 100%;
	padding: 25px 16px 31px;
	&:first-child {
		border-top: 1px solid #e4e1d3;
	}
	&::after {
		content: '';
		display: table;
		clear: both;
	}
}
.basket__top {
	display: none;
}
.basket__box:first-child {
	flex-direction: column;
	width: 60%;
	padding-right: 20px;
	display: inline-block;
	float: left;
}
.basket__box:nth-child(3) {
	position: absolute;
	opacity: 0;
	visibility: hidden;
	z-index: -1;
}
.basket__box:nth-child(2) {
	width: 40%;
	display: inline-block;
	float: right;
	margin-bottom: 15px;
}
.basket__box:nth-child(4) {
	width: 40%;
	display: inline-block;
	float: right;
	margin-bottom: 15px;
}
.basket__bottom {
	flex-direction: column-reverse;
	margin-top: 12px;
}
.basket__right {
	width: 100%;
	padding: 27px 16px 20px;
}
.basket__img {
	border: none;
	width: 100%;
	height: 28vw;
}
.basket__img img {
	max-width: 95%;
	max-height: 95%;
}
.basket__text {
	margin-left: 0;
}
.basket__name {
	font-size: 14px;
	margin-top: 17px;
	margin-bottom: 6px;
}
.basket__art {
	font-size: 14px;
}
.basket__size {
	font-size: 14px;
}
.basket__noprice {
	font-size: 14px;
}
.basket__price {
	font-size: 14px;
}
.basket__all {
	font-size: 14px;
}
.basket__col-input {
	font-size: 15px;
}
.basket__svg {
	top: 12px;
	right: 12px;
}
.basket__buy-left {
	width: 152px;
}
.basket__address {
	font-size: 14px;
	font-weight: normal;
}
.basket__left {
	padding-top: 24px;
}
.basket__link-modal {
	left: -84px;
	transition: none;
}