.footer {
  margin-top: 72px;
}
.footer .container {
  padding: 0;
  width: 100%;
}
.footer__list {
  display: none;
}
.footer__box {
  flex-direction: column;
}
.footer__wrap {
  border-radius: 0;
  padding: 33px 33px 0;
}
.copy {
  background-color: #f1edde;
  flex-direction: column;
  padding-top: 13px;
  padding-bottom: 18px;
}
.copy__block {
  flex-direction: column;
  text-align: center;
}
.copy__block:last-child {
  margin-right: 0;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.copy__block:last-child svg {
  margin: 0 13px 0 0;
}
.copy__link {
  margin: 4px 0 32px;
}
.footer__par {
  display: none;
}
.footer__block-tel {
  margin: 0 auto;
  width: 250px;
  padding-right: 0;
}
.footer__md {
  font-size: 0.875rem;
}
.footer__block-tel .footer__md {
  margin: 0 auto;
  width: 173px;
}
.footer__tel {
  margin: 15px auto 1px auto;
  text-align: center;
}
.footer__time {
  margin: 0 auto;
  text-align: center;
}
.footer__block:last-child {
  margin: 30px auto 0;
  text-align: center;
}
.footer__items {
  justify-content: center;
  margin-top: 11px;
}
.footer__item {
  margin: 0 8px;
}