.about {
	padding: 33px 20px 52px;
	margin-top: 23px;
}
.about .title {
	font-size: 20px;
	font-weight: 500;
	margin-bottom: 19px;
}
.about .desc {
	max-width: 290px;
	margin-left: auto;
	margin-right: auto;
}
.about__tabs {
	margin-top: 27px;
}
.about__par {
	margin: 8px 0 16px;
}
.about__desc br {
	display: none;
}
.about .br-mobile {
	display: block;
}
.about__bold {
	font-size: 18px;
	margin-top: 21px;
	margin-bottom: 5px;
}
.about__req {
	line-height: 2.45;
}
.about__office {
	padding: 0;
}
.about__item-img {
	width: 144px;
	height: 180px;
}
.about__item {
	width: 144px;
	margin-right: 15px;
}
.about__slider .card-product__arrow svg {
	display: none;
}
.about__slider .card-product__arrow {
	width: 240px;
	top: 95px;
}
.about__slider .card-product__arrow--next {
	right: -130px;
}
.about__slider .card-product__arrow--prev {
	left: -129px;
}
.about__info {
	margin-top: -6px;
	font-size: 16px;
}
.about__slider {
	margin-top: 24px;
}
.about__name {
	margin-top: 13px;
}